.logo {
    color: black;
    font-size: 30px;
}
.pages ul li{
    font-size: 20px;
    justify-content: center;
}
nav {
    background-color:blanchedalmond;
    padding: 10px 15px;
    display: flex;
    justify-content: space-between;
  }
  
  ul {
    list-style-type: none;
    margin: 0;
    padding: 10px;
  }
  
  li {
    display: inline;
    margin-right: 20px;
  }
  
  li:last-child {
    margin-right: 0;
  }
  
  a {
    color: #fff;
    text-decoration: none;
  }
  
  a:hover {
    color: black;
  }
  
  /* Page Content */
  .container {
    max-width: 800px;
    margin: 20px auto;
    padding: 0 20px;
  }
  
  h2 {
    color: #333;
  }
  
  p {
    color: #666;
  }

.home h2{
  color: rgb(121, 89, 150);
}

.contact h2{
  color: blueviolet;
}

.about h2{
  color: blueviolet;
}

.name {
  color: white;
  font-size: 15px;
}